import React, { Component } from 'react';
import DetailPresenter from './DetailPresenter';
import { movieApi, tvApi } from 'api';
class DetailContainer extends Component {
	constructor(props) {
		super(props);
		// console.log('constructor: ', props);
		const {
			history: {
				location: { pathname },
			},
		} = props;
		this.state = {
			result: null,
			error: null,
			loading: true,
			isMovie: pathname.includes('/movie/'), //true, false
		};
	}
	async componentDidMount() {
		console.log('componentDidMount: ', this.props);
		const {
			history: { push }, //Í≤ΩÎ°ú ÏóÜÏùÑ Í≤ΩÏö∞ ÎèåÏïÑÍ∞à Fc
			match: {
				params: { id }, // Ïù¥ Í∞íÏúºÎ°ú Ï∞æÏùå
			},
		} = this.props;
		const { isMovie } = this.state;
		const parseId = parseInt(id);
		// console.log(typeof parseId); //number
		if (isNaN(parseId)) {
			return push('/'); //Ìï¥Îãπ ÏïàÎêòÎäî idÍ∞íÏùº Í≤ΩÏö∞ returnÌï† ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
		}
		let result = null;
		try {
			// console.log(await movieApi.movieDetail(22));
			// await movieApi.movieDetail(parseId);
			if (isMovie)
				({ data: result } = await movieApi.movieDetail(parseId));
			else ({ data: result } = await tvApi.showDetail(parseId));
			// console.log(result);
		} catch {
			this.setState({
				error: "can't find Anything",
			});
		} finally {
			this.setState({
				loading: false,
				result,
			});
		}
	}

	render() {
		const { result, error, loading } = this.state;
		console.log(result);
		return (
			<DetailPresenter result={result} error={error} loading={loading} />
		);
	}
}

export default DetailContainer;
